              <p className="info_desc">Lorem, ipsum dolor sit amet consectetur adipisicing elit. Aliquid culpa illo excepturi cum sed dolorum nisi doloribus expedita nihil alias quas numquam earum explicabo dolor doloremque necessitatibus blanditiis, a maiores!</p>
