import React, { Component } from 'react';

import Nav from './Nav';
import Carousel from './Carousel';

class Header extends Component {
  render() {
    return(
      <div>
        <header id="hero">
          <Nav />
          <Carousel />
        </header>
      </div>
    )
  }
}

export default Header;



